.clients,
.usecase {
    padding: 120px 0 0 0;
}

.usecase {
    background-color: var(--background-normal-normal);
    text-align: center;

    body.dark-mode & {
        background-color: var(--dark-background-normal-normal);
        color: var(--dark-label-normal);
    }

    .section-title {
        font-size: 12px;
        font-weight: 600;
        color: var(--primary-normal);
        background: var(--background-normal-normal);
        padding: 8px 16px;
        border: 1px solid var(--line-normal-normal);
        border-radius: 20px;
        text-transform: uppercase;
        box-shadow: 0 8px 8px var(--fill-alternative);
        margin-bottom: 32px;
        display: inline-block;
        max-width: 100%;
        text-align: center;

        body.dark-mode & {
            background: var(--dark-background-normal-normal);
            border-color: var(--dark-line-normal-normal);
            color: var(--primary-normal);
            box-shadow: 0 8px 8px var(--dark-fill-alternative);
        }
    }

    .section-subtitle {
        font-size: 32px;
        font-weight: bold;
        margin-bottom: 40px;
        line-height: 1.2;
        color: var(--label-normal);
        text-align: center;

        body.dark-mode & {
            color: var(--dark-label-normal);
        }

        span {
            background: linear-gradient(135deg, #1D74FF 0%, #3B37FF 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }

    .usecase-items {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 20px;
    }

    .usecase-item {
        width: 30%;
        max-width: 400px;
        margin-bottom: 40px;
        background-color: var(--background-normal-normal);
        padding-top: 16px;
        border-radius: 30px;
        border: 1px solid var(--line-normal-normal);
        display: flex;
        flex-direction: column;
        align-items: center;

        body.dark-mode & {
            background-color: var(--dark-background-normal-normal);
            border-color: var(--dark-line-normal-normal);
        }

        h3 {
            color: var(--label-strong);
            body.dark-mode & {
                color: var(--dark-label-strong);
            }
        }
    }

    img {
        width: calc(100% - 32px);
        height: auto;
        object-fit: contain;
        margin-bottom: 16px;
    }
    
    .modal-content img {
        width: 100%;
        object-fit: cover;
        margin-bottom: 20px;
    }

    .text-content {
        padding: 0 16px;
        width: calc(100% - 32px);
        margin: 0 auto;
        flex-grow: 1;
    }

    h3 {
        width: 100%;
        font-size: 16px;
        line-height: 1.4;
        margin-bottom: 8px;
        text-align: start;
        padding: 0 20px;
        color: var(--label-normal);

        body.dark.mode & {
            color: var(--dark-label-normal);
        }
    }

    p {
        font-size: 18px;
        line-height: 1.8;
        text-align: start;
        padding: 0 20px 40px 20px;
        color: #999;
        margin-bottom: 24px;
    }
    .read-more {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: var(--primary-normal);
        color: var(--static-white);
        text-decoration: none;
        font-weight: 600;
        font-size: 16px;
        margin-top: auto;
        padding: 12px 16px;
        border-radius: 40px;
        box-shadow: 0px 10px 20px rgba(29, 116, 255, 0.1);
        width: calc(100% - 32px);
        margin-left: 16px;
        margin-right: 16px;
        margin-bottom: 16px;
        border: 1px solid var(--line-normal-normal);

        body.dark.mode & {
            background-color: var(--primary-normal);
            color: var(--dark-primary-normal);
        }

        &::after {
            content: '>';
            margin-left: 8px;
            font-size: 18px;
        }

        &:hover {
            transform: translateY(-2px);
            box-shadow: 0px 15px 30px rgba(29, 116, 255, 0.2);
        }
    }

    .usecase-description {
        font-size: 16px;
        line-height: 1.5;
        height: 3em;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
}

@media (max-width: 768px) {
    .usecase {
        .usecase-items {
            flex-direction: column;
            align-items: center;
            gap: 20px;
            padding-bottom: 0;
        }

        .usecase-item {
            width: 100%;
            max-width: 400px;
            margin-bottom: 20px;
        }
    }
}

@media (max-width: 1200px) {
    .container {
        padding: 0;
    }

    .wallet,
    .block-explorer,
    .browser,
    .solution {
        width: 94%;
        padding: 40px 24px;
        border-radius: 25px;
    }
}

@media (max-width: 768px) {
    .hero h1 {
        font-size: 36px;
    }

    .clients,
    .usecase {
        padding: 40px 0;
    }

    .clients .section-subtitle,
    .usecase .section-subtitle {
        font-size: 24px;
    }

    .usecase .usecase-items {
        overflow-x: auto;
        display: flex;
        flex-wrap: nowrap;
        gap: 24px;
        padding-bottom: 16px;
    }

    .usecase .usecase-item {
        flex: 0 0 auto;
        width: 90%;
        max-width: none;
        margin-bottom: 0;
    }
}
